<template>
    <div class="notice">
        <navigate title="消息通知" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-list toggle-nested>
                <mu-list-item :open="notice.push" nested>
                    <mu-list-item-title>是否接收推送</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-switch :input-value="notice.push" @change="handlerToggle('push')"/>
                    </mu-list-item-action>
                    <mu-list-item slot="nested">
                        <mu-list-item-title>铃声提示</mu-list-item-title>
                        <mu-list-item-action>
                            <mu-switch :input-value="notice.ring"
                                       :disabled="!notice.push"
                                       @change="handlerToggle('ring')"/>
                        </mu-list-item-action>
                    </mu-list-item>
                    <mu-list-item slot="nested">
                        <mu-list-item-title>震动提示</mu-list-item-title>
                        <mu-list-item-action>
                            <mu-switch :input-value="notice.shock"
                                       :disabled="!notice.push"
                                       @change="handlerToggle('shock')"/>
                        </mu-list-item-action>
                    </mu-list-item>
                    <mu-list-item slot="nested">
                        <mu-list-item-title>消息弹窗提示</mu-list-item-title>
                        <mu-list-item-action>
                            <mu-switch :input-value="notice.popup"
                                       :disabled="!notice.push"
                                       @change="handlerToggle('popup')"/>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list-item>
            </mu-list>
            <mu-list>
                <mu-list-item class="list-item">
                    <mu-list-item-content>
                        <mu-list-item-title>勿扰模式</mu-list-item-title>
                        <mu-list-item-sub-title>晚上 20:00 ~ 早上 8:00</mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-switch :input-value="notice.disturbed"
                                   @change="handlerToggle('disturbed')"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {noticeMixins} from "../../../common/mixins"

    export default {
        name: "notice",
        mixins: [noticeMixins],
        methods: {
            handlerToggle(name) {
                this.setNovice(Object.assign({}, this.notice, {[name]: !this.notice[name]}))

                console.log(`%c当前 ${name} 为 ${this.notice[name]}`, 'color: #673ab7')
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .notice
        padding-top 46px
        background-color #efefef
</style>



